.scroll {
  display: flex;
  justify-content: flex-end;

  &__link {
    width: 30%;
    height: 30%;

    background-image: url(../images/arow.svg);
    background-repeat: no-repeat;
    background-position: center;
    box-sizing: border-box;
  }
  @include hover(transform, scale(120%));
}
